.dialogsWrapper {
  display: grid;
  grid-template-columns: 1fr 5fr;
}

.dialogs {
  padding: 10px 40px;
  margin: 0;
  border-right: 1px solid #e1e1e1;
}

.dialog {
  padding-bottom: 1.2rem;
  cursor: pointer;

  &.active {
    font-weight: bold;
    color: #009e6b;
  }
}

.messages {
  padding-left: 1.5rem;
}

.message {
  padding: 10px 0;
}